
/* WHERE ANIMATIONS HAPPEN */

/*animation for new block */
.new {
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
  -webkit-animation: new linear forwards;
          animation: new linear forwards;
  -webkit-animation-duration: 0.25s;
          animation-duration: 0.25s;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

/*animation for adding a new score*/
.addScore {
  color: black;
  position: absolute;
  z-index: 10;
  -webkit-animation: addScore 0.3s linear forwards;
          animation: addScore 0.3s linear forwards;
}

/*animation for a block that combined with another*/
.upgraded {
  -webkit-animation: upgraded 0.15s 0.1s linear forwards;
          animation: upgraded 0.15s 0.1s linear forwards;
}

.hor-1 {
  -webkit-animation: slideRight1 0.1s linear forwards;
          animation: slideRight1 0.1s linear forwards;
}

.hor-2 {
  -webkit-animation: slideRight2 0.1s linear forwards;
          animation: slideRight2 0.1s linear forwards;
}

.hor-3 {
  -webkit-animation: slideRight3 0.1s linear forwards;
          animation: slideRight3 0.1s linear forwards;
}

.hor1 {
  -webkit-animation: slideLeft1 0.1s linear forwards;
          animation: slideLeft1 0.1s linear forwards;
}

.hor2 {
  -webkit-animation: slideLeft2 0.1s linear forwards;
          animation: slideLeft2 0.1s linear forwards;
}

.hor3 {
  -webkit-animation: slideLeft3 0.1s linear forwards;
          animation: slideLeft3 0.1s linear forwards;
}


.ver1 {
  -webkit-animation: slideUp1 0.1s linear forwards;
          animation: slideUp1 0.1s linear forwards;
}

.ver2 {
  -webkit-animation: slideUp2 0.1s linear forwards;
          animation: slideUp2 0.1s linear forwards;
}

.ver3 {
  -webkit-animation: slideUp3 0.1s linear forwards;
          animation: slideUp3 0.1s linear forwards;
}


.ver-1 {
  -webkit-animation: slideDown1 0.1s linear forwards;
          animation: slideDown1 0.1s linear forwards;
}

.ver-2 {
  -webkit-animation: slideDown2 0.1s linear forwards;
          animation: slideDown2 0.1s linear forwards;
}

.ver-3 {
  -webkit-animation: slideDown3 0.1s linear forwards;
          animation: slideDown3 0.1s linear forwards;
}

/* Shifting Animations for left,right, up, down */
@-webkit-keyframes new {
  from { -webkit-transform:  scale(0); transform:  scale(0); }
  to { -webkit-transform: scale(1); transform: scale(1); }
}
@keyframes new {
  from { -webkit-transform:  scale(0); transform:  scale(0); }
  to { -webkit-transform: scale(1); transform: scale(1); }
}

/* Shifting Animation for block that is combined */
@-webkit-keyframes upgraded {
  0% { -webkit-transform: scale(1.4); transform: scale(1.4) }
  50% { -webkit-transform: scale(0.7); transform: scale(0.7) }
  100% { -webkit-transform: scale(1); transform: scale(1) }
}
@keyframes upgraded {
  0% { -webkit-transform: scale(1.4); transform: scale(1.4) }
  50% { -webkit-transform: scale(0.7); transform: scale(0.7) }
  100% { -webkit-transform: scale(1); transform: scale(1) }
}

/*animation for scores sliding up */
@-webkit-keyframes addScore {
  0% { -webkit-transform: translateY(0); transform: translateY(0); opacity: 0.2; }
  75% { -webkit-transform: translateY(-37.5px); transform: translateY(-37.5px); opacity: 1; }
  100% { -webkit-transform: translateY(-75px); transform: translateY(-75px); opacity: 0; }
}
@keyframes addScore {
  0% { -webkit-transform: translateY(0); transform: translateY(0); opacity: 0.2; }
  75% { -webkit-transform: translateY(-37.5px); transform: translateY(-37.5px); opacity: 1; }
  100% { -webkit-transform: translateY(-75px); transform: translateY(-75px); opacity: 0; }
}

/* Shifting Animations for left,right, up, down */
@-webkit-keyframes slideRight1 {
  from { -webkit-transform:  translateX(5px); transform:  translateX(5px); }
  to { -webkit-transform: translateX(110px); transform: translateX(110px); }
}
@keyframes slideRight1 {
  from { -webkit-transform:  translateX(5px); transform:  translateX(5px); }
  to { -webkit-transform: translateX(110px); transform: translateX(110px); }
}

@-webkit-keyframes slideRight2 {
  from { -webkit-transform:  translateX(5px); transform:  translateX(5px); }
  to { -webkit-transform: translateX(220px); transform: translateX(220px); }
}

@keyframes slideRight2 {
  from { -webkit-transform:  translateX(5px); transform:  translateX(5px); }
  to { -webkit-transform: translateX(220px); transform: translateX(220px); }
}

@-webkit-keyframes slideRight3 {
  from { -webkit-transform:  translateX(5px); transform:  translateX(5px); }
  to { -webkit-transform: translateX(330px); transform: translateX(330px); }
}

@keyframes slideRight3 {
  from { -webkit-transform:  translateX(5px); transform:  translateX(5px); }
  to { -webkit-transform: translateX(330px); transform: translateX(330px); }
}

@-webkit-keyframes slideLeft1 {
  from { -webkit-transform: translateX(5px); transform: translateX(5px); }
  to { -webkit-transform: translateX(-110px); transform: translateX(-110px); }
}

@keyframes slideLeft1 {
  from { -webkit-transform: translateX(5px); transform: translateX(5px); }
  to { -webkit-transform: translateX(-110px); transform: translateX(-110px); }
}

@-webkit-keyframes slideLeft2 {
  from { -webkit-transform:  translateX(5px); transform:  translateX(5px); }
  to { -webkit-transform: translateX(-220px); transform: translateX(-220px); }
}

@keyframes slideLeft2 {
  from { -webkit-transform:  translateX(5px); transform:  translateX(5px); }
  to { -webkit-transform: translateX(-220px); transform: translateX(-220px); }
}

@-webkit-keyframes slideLeft3 {
  from { -webkit-transform:  translateX(5px); transform:  translateX(5px); }
  to { -webkit-transform: translateX(-330px); transform: translateX(-330px); }
}

@keyframes slideLeft3 {
  from { -webkit-transform:  translateX(5px); transform:  translateX(5px); }
  to { -webkit-transform: translateX(-330px); transform: translateX(-330px); }
}

/*Vertical Animations */

@-webkit-keyframes slideUp1 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(-110px); transform: translateY(-110px); }
}

@keyframes slideUp1 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(-110px); transform: translateY(-110px); }
}

@-webkit-keyframes slideUp2 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(-220px); transform: translateY(-220px); }
}

@keyframes slideUp2 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(-220px); transform: translateY(-220px); }
}

@-webkit-keyframes slideUp3 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(-330px); transform: translateY(-330px); }
}

@keyframes slideUp3 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(-330px); transform: translateY(-330px); }
}

@-webkit-keyframes slideDown1 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(110px); transform: translateY(110px); }
}

@keyframes slideDown1 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(110px); transform: translateY(110px); }
}

@-webkit-keyframes slideDown2 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(220px); transform: translateY(220px); }
}

@keyframes slideDown2 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(220px); transform: translateY(220px); }
}

@-webkit-keyframes slideDown3 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(330px); transform: translateY(330px); }
}

@keyframes slideDown3 {
  from { -webkit-transform:  translateY(5px); transform:  translateY(5px); }
  to { -webkit-transform: translateY(330px); transform: translateY(330px); }
}



