
.emoji {
  float: left;
  position: absolute;
  left: 0px;
  top: -24px;
  width: 30px;
  height: 25px;
}

/*animation for marching parrots*/
.emojiMarch {
  -webkit-animation: emojiMarch 7s linear infinite;
          animation: emojiMarch 7s linear infinite;
}

/*animation for making parrot move around the board*/
@-webkit-keyframes emojiMarch {
    0% { left:0px; top:-24px; -webkit-transform: rotate(0deg); transform: rotate(0deg);}
    24% { left:447px; top:-24px; -webkit-transform: rotate(0deg); transform: rotate(0deg);}
    25%  { left:447px; top:-24px; -webkit-transform: rotate(90deg); transform: rotate(90deg); }
    49%  { left:447px; top:447px; -webkit-transform: rotate(90deg); transform: rotate(90deg);}
    50%  { left:447px; top:448px; -webkit-transform: rotate(180deg); transform: rotate(180deg);}
    74%  { left:-25px; top:448px;-webkit-transform: rotate(180deg);transform: rotate(180deg); }
    75%  { left:-25px; top:447px; -webkit-transform: rotate(270deg); transform: rotate(270deg)}
    99% { left:-25px; top:-24px; -webkit-transform: rotate(270deg); transform: rotate(270deg) }
    100% { left:-25px; top:-24px; -webkit-transform: rotate(0deg); transform: rotate(0deg) }
}
@keyframes emojiMarch {
    0% { left:0px; top:-24px; -webkit-transform: rotate(0deg); transform: rotate(0deg);}
    24% { left:447px; top:-24px; -webkit-transform: rotate(0deg); transform: rotate(0deg);}
    25%  { left:447px; top:-24px; -webkit-transform: rotate(90deg); transform: rotate(90deg); }
    49%  { left:447px; top:447px; -webkit-transform: rotate(90deg); transform: rotate(90deg);}
    50%  { left:447px; top:448px; -webkit-transform: rotate(180deg); transform: rotate(180deg);}
    74%  { left:-25px; top:448px;-webkit-transform: rotate(180deg);transform: rotate(180deg); }
    75%  { left:-25px; top:447px; -webkit-transform: rotate(270deg); transform: rotate(270deg)}
    99% { left:-25px; top:-24px; -webkit-transform: rotate(270deg); transform: rotate(270deg) }
    100% { left:-25px; top:-24px; -webkit-transform: rotate(0deg); transform: rotate(0deg) }
}



